import React, { useEffect, useState } from 'react';
import { domain } from '../../config';
import { VictoryBar, VictoryChart, VictoryTheme } from 'victory';
import { Link} from "react-router-dom";

export default function Statistics() {

  const [dataLen, setLen] = useState([]);
  useEffect(() => {
    async function fn() {
      const req = await fetch(domain + '/vacationFollowed');
      const dataReq = await req.json();
      setLen(dataReq[0].map(item => {
        return ({ quarter: 'vacation ' + item.vac_id, earnings: item.followers_mount })
      }))
    }
    fn();
  }, [])

  return (
    <div>
      <Link to="/component/VacationAdmin">vacation</Link>
      <div style={{ width: '50%', height: '40%' }}>
        <VictoryChart
          theme={VictoryTheme.material}
          domainPadding={{ x: 30 }}
        >
          <VictoryBar
            style={{ data: { fill: "#c43a31", stroke: "black", strokeWidth: 2 } }} barWidth={({ index }) => index * 2 + 8}
            data={dataLen}
            alignment="start"
            x="quarter"
            y="earnings"
          />
        </VictoryChart>
      </div>
    </div>
  )
}