import React, { useState, useEffect } from 'react';
import LogOut from '../LogOut';
import Card from 'react-bootstrap/Card';
import Modal from 'react-bootstrap/Modal';
import { Button, Form, Col } from 'react-bootstrap';
import { domain } from '../../config';
import { useHistory, Link } from "react-router-dom";

export default function VacationAdmin() {
    const history = useHistory();
    let user = JSON.parse(localStorage.getItem('userInfo'));

    if (localStorage.getItem('userInfo') === null || JSON.parse(localStorage.getItem('userInfo')).isAdmin == false) {
        history.push("LogIn");
    }
    const [description, setDescription] = useState('');
    const [location, setLocation] = useState('');
    const [image, setImage] = useState('');
    const [start_at, setStart] = useState('');
    const [end_at, setEnd] = useState('');
    const [price, setPrice] = useState('');
    const [vacList, setVacList] = useState([]);
    const [deleteVac, setDeleteVac] = useState(0);
    const [editVac, seteditVaction] = useState(0);
    const [currentVac, setCurrentVac] = useState(0);
    const [vac_id, setVac_id] = useState(0);
    const [show, setShow] = useState(false);
    const [whichButton, setWhichButton] = useState('');
    const handleClose = () => setShow(false);
    function handleShow(isEdit, item) {
        if (isEdit === 'edit') {
            setDescription(item.description)
            setLocation(item.location)
            setImage(item.image)
            setStart(item.start_at)
            setEnd(item.end_at)
            setPrice(item.price)
        }
        setShow(true);
    }

    useEffect(() => {
        async function vacationAdmin() {
            const req = await fetch(domain + `/vactionAdmin`);
            const data = await req.json();
            setVacList(data);
        }
        vacationAdmin();
    }, [deleteVac, editVac])

    async function dleteVacation(vacId) {
        await fetch('http://localhost:3001/deleteVacation', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ vacId })
        });
        setDeleteVac(new Date())
    }

    async function addOrEditVaction() {
        const req = await fetch(domain + `/${whichButton}`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                description,
                location,
                image,
                start_at,
                end_at,
                price,
                vac_id
            })
        });

        const data = await req.json();
        console.log(data);
        setShow(false)
        seteditVaction(new Date())
    }

    return (
        <div >
            <LogOut />
            <button onClick={() => { handleShow(); setWhichButton('addVacation');setCurrentVac('') }} style={{ marginTop: '2%', marginRight: '2%', float: 'right' }}>add vacation</button>
            <Link to="/component/Statistics">Statistics</Link>
            <h1 style={{ textAlign: 'center', marginTop: '0%' }}>hello {user.userName}</h1>
            { vacList.map(item => {
                return (
                    <div style={{ float: 'left', marginLeft: '5px' }}>
                        <Card className='Card' style={{ width: '15rem' }}>
                            <Button size='sm' variant=''
                                onClick={() => dleteVacation(item.vac_id)}>‚ùå</Button>
                            <Card.Img variant="top" key={item.image} src={item.image} />

                            <Button size='sm' variant='' onClick={() => { handleShow('edit', item); setCurrentVac(item); setWhichButton('editVacation'); setVac_id(item.vac_id) }}
                                style={{ color: 'gray', float: 'right', marginRight: '5px' }}>üñã</Button>
                            <Card.Text>
                                <p style={{ float: 'right' }}>followers: {item.followers_mount}</p>
                        location: {item.location}<br />
                        description: {item.description}<br />
                        ${item.price}<br />
                        start: {item.start_at}<br />
                         end: {item.end_at}<br />
                                <p style={{ textAlign: 'left' }}>{item.vac_id}</p>
                            </Card.Text>
                        </Card>
                    </div>

                )
            })
            }


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                </Modal.Header>
                <Modal.Body>
                    <Form style={{ marginLeft: '2%', marginRight: '2%' }} >
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom01">
                                <Form.Label>Description</Form.Label>
                                <Form.Control
                                    onChange={(e) => { setDescription(e.target.value) }}
                                    required
                                    type="text"
                                    placeholder="Description"
                                    defaultValue={currentVac.description}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>Price</Form.Label>
                                <Form.Control
                                    onChange={(e) => setPrice(e.target.value)}
                                    type="number"
                                    placeholder="Price"
                                    required
                                    defaultValue={currentVac.price}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom02">
                                <Form.Label>Location</Form.Label>
                                <Form.Control
                                    onChange={(e) => setLocation(e.target.value)}
                                    required
                                    type="text"
                                    placeholder="Location"
                                    defaultValue={currentVac.location}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustomUsername">
                                <Form.Label>Image</Form.Label>
                                <Form.Control
                                    onChange={(e) => setImage(e.target.value)}
                                    type="text"
                                    placeholder="Image"
                                    aria-describedby="inputGroupPrepend"
                                    required
                                    defaultValue={currentVac.image}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>Start</Form.Label>
                                <Form.Control
                                    onChange={(e) => setStart(e.target.value)}
                                    type="date"
                                    placeholder="Start"
                                    required
                                    defaultValue={currentVac.start_at}
                                />
                            </Form.Group>
                            <Form.Group as={Col} md="4" controlId="validationCustom03">
                                <Form.Label>End</Form.Label>
                                <Form.Control
                                    onChange={(e) => setEnd(e.target.value)}
                                    type="date"
                                    placeholder="End"
                                    required
                                    defaultValue={currentVac.end_at}
                                />
                            </Form.Group>
                        </Form.Row>
                        <Button type='button' onClick={() => addOrEditVaction()}>send</Button>
                    </Form>
                </Modal.Body>
            </Modal>
        </div >
    )
}